import React, {useState, useEffect} from "react";
import { useDispatch } from "react-redux";
import { removeCurrencyPair, reverseCurrencyPair, updateCurrencyPair } from "./appstore/currencyPairsSlice";
import AppConfig from "../config.json";
import ButtonBase from '@mui/material/ButtonBase';
import {ReactComponent as DoubleEndedArrow} from "./images/double-ended-arrow.svg";
import ReplayIcon from '@mui/icons-material/Replay';
import HighlightOffIcon from '@mui/icons-material/HighlightOff';
import TextField from '@mui/material/TextField';
import {ReactComponent as Loader} from "./images/loader.svg";
import "./forexRateCalculator.scss";

export default function ForexRateCalculator({from, to, backgroundColor, id, updatedAt}) {
  const [exchangeRate, setExchangeRate] = useState(null);
  const [fromInput, setFromInput] = useState(null);
  const [toInput, setToInput] = useState(null);
  const dispatch = useDispatch();

  const getCurrencyExchangeRateDetails = () => {
    fetch(`${AppConfig["forex-api-domain"]}/convert?from=${from}&to=${to}`)
      .then(resp => resp.json())
      .then(resp => {
        setExchangeRate(resp.result);
        setFromInput(1);
        setToInput(resp.result);
        dispatch(updateCurrencyPair({
          id: id,
          rate: resp.result,
          updatedAt: new Date().toString()
        }));
      })
      .catch(err => {
        console.log("Get CurrencyPairExvhangeRate error:");
        console.log(err);
        return [];
    });
  }

  useEffect(()=>{
    getCurrencyExchangeRateDetails();
  },[from, to]);

  return (
    <div className="forexRateCalculatorContainer" style={{backgroundColor: backgroundColor}}>
      <div className="currencyPairLabels">
        <div className="label from">{from}</div>
        <div className="doubleEndedArrow">
          <ButtonBase onClick={()=>dispatch(reverseCurrencyPair(id))}>
            <DoubleEndedArrow />
          </ButtonBase>
        </div>
        <div className="label to">{to}</div>
        {
          exchangeRate !== null &&
            <div className="rate">{exchangeRate}</div>
        }
      </div>
      {
        exchangeRate === null &&
          <div className="loaderContainer"><Loader /></div>
      }
      <div className="currencyPairActions" hidden={exchangeRate===null}>
        <div className="topActions">
          <ButtonBase onClick={getCurrencyExchangeRateDetails}><ReplayIcon /></ButtonBase>
          <div className="spacer"></div>
          <ButtonBase onClick={()=>dispatch(removeCurrencyPair(id))}><HighlightOffIcon /></ButtonBase>
        </div>
        <div className="textboxes">
          <TextField
            id="from-currency"
            size="small"
            value={fromInput || ""}
            onChange={(event) => {
              if(isNaN(event.target.value)){
                return;
              }
              setFromInput(event.target.value);
              setToInput(event.target.value * exchangeRate)
            }}
            fullWidth
          />
          <div className="spacer" />
          <TextField
            id="to-currency"
            size="small"
            value={toInput || ""}
            onChange={(event) => {
              if(isNaN(event.target.value)){
                return;
              }
              setFromInput(event.target.value / exchangeRate);
              setToInput(event.target.value)
            }}
            fullWidth
          />
        </div>
        <div className="lastUpdatedDetails">
          <div className="label">Last Updated</div>
          <div className="value">{new Date(updatedAt).toLocaleDateString() + " " + new Date(updatedAt).toLocaleTimeString()}</div>
        </div>
      </div>
    </div>
  );
}