import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  isOpen: false,
}

export const addCurrencyPairsModalSlice = createSlice({
  name: "addCurrencyPairsModal",
  initialState,
  reducers: {
    openModal: (state) => {
      state.isOpen = true;
    },
    closeModal: (state) => {
      state.isOpen = false;
    }
  },
})

export const { openModal, closeModal } = addCurrencyPairsModalSlice.actions

export default addCurrencyPairsModalSlice.reducer