export function getCurrencyPairs(){
  try{
    let currencyPairs = localStorage.getItem("currency-pairs");
    currencyPairs = JSON.parse(currencyPairs);
    if(Array.isArray(currencyPairs)){
      currencyPairs = currencyPairs.filter(currencyPair => {
        if(typeof currencyPair !== "object"){
          return false;
        }
        if(
          currencyPair.hasOwnProperty("from")
          && currencyPair.hasOwnProperty("to")
          && currencyPair.hasOwnProperty("backgroundColor")
          && currencyPair.hasOwnProperty("createdAt")
          && currencyPair.hasOwnProperty("id")
        ){
          return true
        }
      });
      setCurrencyPairs(currencyPairs);
      return currencyPairs;
    }
    setCurrencyPairs([]);
    return [];
  }
  catch(err){
    console.log("Bad value in Local Storage");
    setCurrencyPairs([]);
    return [];
  }
  
}

export function setCurrencyPairs(currencyPairs){
  localStorage.setItem("currency-pairs", JSON.stringify(currencyPairs));
}