import { createSlice } from "@reduxjs/toolkit";
import { getCurrencyPairs, setCurrencyPairs } from "../localstorage/localStorage";

const initialState = {
  currencyPairs: getCurrencyPairs()
}

export const currencyPairsSlice = createSlice({
  name: "currencyPairs",
  initialState,
  reducers: {
    clearAll: (state) => {
      state.currencyPairs = [];
      setCurrencyPairs(state.currencyPairs);
    },
    addCurrencyPair: (state, action) => {
      state.currencyPairs.push({
        ...action.payload,
        createdAt: new Date().toString(),
        id: new Date().getTime()
      });
      setCurrencyPairs(state.currencyPairs);
    },
    removeCurrencyPair: (state, action) => {
      let i = -1;
      state.currencyPairs.forEach((currencyPair, index) => {if(currencyPair.id === action.payload){i = index;}});
      if(i>-1){
        state.currencyPairs.splice(i, 1);
      }
      setCurrencyPairs(state.currencyPairs);
    },
    reverseCurrencyPair: (state, action) => {
      let i = -1;
      state.currencyPairs.forEach((currencyPair, index) => {if(currencyPair.id === action.payload){i = index;}});
      if(i>-1){
        let oldCurrencyPair = state.currencyPairs[i];
        let temp = oldCurrencyPair.from;
        let newCurrencyPair = {
          ...oldCurrencyPair,
          from: oldCurrencyPair.to,
          to: temp
        }
        state.currencyPairs.splice(i, 1, newCurrencyPair);
      }
      setCurrencyPairs(state.currencyPairs);
    },
    updateCurrencyPair: (state, action) => {
      let i = -1;
      state.currencyPairs.forEach((currencyPair, index) => {if(currencyPair.id === action.payload.id){i = index;}});
      if(i>-1){
        let oldCurrencyPair = state.currencyPairs[i];
        let newCurrencyPair = {
          ...oldCurrencyPair,
          ...action.payload
        }
        state.currencyPairs.splice(i, 1, newCurrencyPair);
      }
      setCurrencyPairs(state.currencyPairs);
    }
  },
})

export const { clearAll, addCurrencyPair, removeCurrencyPair, reverseCurrencyPair, updateCurrencyPair } = currencyPairsSlice.actions

export default currencyPairsSlice.reducer