import React, { useState, useEffect} from "react";
import { useSelector, useDispatch } from "react-redux";
import { closeModal } from "./appstore/addCurrencyPairModalSlice";
import { addCurrencyPair } from "./appstore/currencyPairsSlice";
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import AppConfig from "../config.json";
import {ReactComponent as Loader} from "./images/loader.svg";
import "./addCurrencyPairModal.scss";

const getSupportedCurrencies = () => {
  return fetch(AppConfig["forex-api-domain"] + "/symbols")
    .then(resp => resp.json())
    .then(resp => Object.values(resp.symbols))
    .catch(err => {
      console.log("Get Supported Currencies Error:");
      console.log(err);
      return [];
    });
}

export default function AddCurrencyPairModal(props){
  const currencyPairs = useSelector((state) => state.currencyPairs.currencyPairs);
  const [symbolsList, setSymbolsList] = useState([]);
  const [fromCurrency, setFromCurrency] = useState(null);
  const [toCurrency, setToCurrency] = useState(null);
  const [backgroundColor, setBackgroundColor] = useState("#ffffff");
  const dispatch = useDispatch();

  useEffect(()=>{
    getSupportedCurrencies()
      .then(resp => setSymbolsList(resp))
      .catch(err => {
        console.log("Add Currency Pairs Modal Mount Error:");
        console.log(err);
      })
  },[]);

  return(
    <div id="addCurrencyPairModalContainer">
      <h2>Select the currency pair:</h2>
      {
        symbolsList.length === 0 ?
          (<div className="loaderContainer"><Loader /></div>)
          : (
            <div>
              <Autocomplete
                value={fromCurrency}
                disableClearable={true}
                id="from-currency"
                options={symbolsList.filter(option => {
                  if(toCurrency !== null){
                    if(option.code === toCurrency.code){
                      return false;
                    }
                    let alreadySelectedPair = currencyPairs.reduce((acc,current) => {
                      if(current.from === toCurrency.code) acc.push(current.to);
                      if(current.to === toCurrency.code) acc.push(current.from);
                      return acc;
                    },[]);
                    if(alreadySelectedPair.includes(option.code)){
                      return false;
                    }
                    return true;
                  }
                  return true;
                })}
                getOptionLabel={(option => option.code + " - " + option.description)}
                onChange={(event,newValue) => setFromCurrency(newValue)}
                renderInput={(params) => <TextField {...params} label="From Currency" />}
              />
              <div className="spacer" />
              <Autocomplete
                value={toCurrency}
                disableClearable={true}
                id="to-currency"
                options={symbolsList.filter(option => {
                  if(fromCurrency !== null){
                    if(option.code === fromCurrency.code){
                      return false;
                    }
                    let alreadySelectedPair = currencyPairs.reduce((acc,current) => {
                      if(current.from === fromCurrency.code) acc.push(current.to);
                      if(current.to === fromCurrency.code) acc.push(current.from);
                      return acc;
                    },[]);
                    if(alreadySelectedPair.includes(option.code)){
                      return false;
                    }
                    return true;
                  }
                  return true;
                })}
                getOptionLabel={(option => option.code + " - " + option.description)}
                onChange={(event,newValue) => setToCurrency(newValue)}
                renderInput={(params) => <TextField {...params} label="To Currency" />}
              />
              <div className="spacer" />
              <TextField
                id="background-color-picker"
                label="Background Color"
                value={backgroundColor}
                onChange={(event) => setBackgroundColor(event.target.value)}
                type="color"
                fullWidth
              />
              <div className="spacer" />
              <div className="spacer" />
              <Stack direction="row-reverse" spacing={2}>
                <Button
                  variant="contained"
                  onClick={()=>dispatch(closeModal())}
                >
                  Cancel
                </Button>
                <Button
                  variant="contained" 
                  disabled={toCurrency===null || fromCurrency===null}
                  onClick={()=>{
                    dispatch(addCurrencyPair({
                      from: fromCurrency.code,
                      to: toCurrency.code,
                      backgroundColor: backgroundColor
                    }));
                    dispatch(closeModal());
                  }}
                >
                  Add
                </Button>
              </Stack>
            </div>
          )
      }
    </div>
  );
}