import React from "react";
import { useSelector, useDispatch } from "react-redux";
import { closeModal } from "./appstore/addCurrencyPairModalSlice";
import ForexRateDashboard from "./forexRateDashboard";
import Modal from '@mui/material/Modal';
import AddCurrencyPairModal from "./addCurrencyPairModal";

export default function ForexRateConversion(props){
  const isAddCurrencyPairsModalOpen = useSelector((state) => state.addCurrencyPairModal.isOpen);
  const dispatch = useDispatch();

  return(
    <div>
      <ForexRateDashboard />
      <Modal
        open={isAddCurrencyPairsModalOpen}
        onClose={() => dispatch(closeModal())}
        aria-labelledby="add-currency-modal"
        aria-describedby="add-currency-modal-description"
      >
        <><AddCurrencyPairModal /></>
      </Modal>
    </div>
  );
}