import { configureStore } from "@reduxjs/toolkit";
import CurrencyPairsReducer from "./currencyPairsSlice";
import addCurrencyPairModalSlice from "./addCurrencyPairModalSlice";

export const store = configureStore({
  reducer: {
    currencyPairs: CurrencyPairsReducer,
    addCurrencyPairModal: addCurrencyPairModalSlice,
  }
});