import React, { useState, useEffect } from "react";
import ForexRateCalculator from "./forexRateCalculator";
import { useSelector, useDispatch } from "react-redux";
import { openModal } from "./appstore/addCurrencyPairModalSlice";
import { clearAll } from "./appstore/currencyPairsSlice";
import Fab from "@mui/material/Fab";
import Button from '@mui/material/Button';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import AddIcon from "@mui/icons-material/Add";
import "./forexRateDashboard.scss";

const sortingOptions = [
  { title: "SORT BY CREATED", orderBy: "createdAt", orderDataType: "date" },
  { title: "SORT BY RATE", orderBy: "rate", orderDataType: "numeric" },
  { title: "SORT ALPHABETICALLY", orderBy: "from", orderDataType: "string" },
  { title: "SORT BY LAST UPDATED", orderBy: "updatedAt", orderDataType: "date" }
]

export default function ForexRateDashboard(props) {
  const currencyPairs = useSelector((state) => state.currencyPairs.currencyPairs);
  const isAddCurrencyPairsModalOpen = useSelector((state) => state.addCurrencyPairModal.isOpen);
  const [sortedPairs, setSortedPairs] = useState([]);
  const [orderBy, setOrderBy] = useState("createdAt");
  const [orderDataType, setOrderDataType] = useState("date");
  const [orderDirection, setOrderDirection] = useState("asc");
  const dispatch = useDispatch();

  useEffect(() => {
    let tempSortedPairs = currencyPairs.slice().sort((a, b) => {
      if (a[orderBy] === undefined || b[orderBy] === undefined) {
        return 0;
      }
      if (orderDataType === "date") {
        return orderDirection === "asc" ?
          new Date(a[orderBy]).getTime() - new Date(b[orderBy]).getTime()
          : new Date(b[orderBy]).getTime() - new Date(a[orderBy]).getTime()
      }
      if (orderDataType === "numeric") {
        return orderDirection === "asc" ?
          a[orderBy] - b[orderBy]
          : b[orderBy] - a[orderBy]
      }
      if (orderDataType === "string") {
        return orderDirection === "asc" ?
          a[orderBy].localeCompare(b[orderBy])
          : b[orderBy].localeCompare(a[orderBy])
      }
    });
    setSortedPairs(tempSortedPairs);
    if(tempSortedPairs.length === 0){
      dispatch(openModal());
    }
  }, [currencyPairs, orderBy, orderDataType, orderDirection]);

  return (
    <div id="forexRateDashboardContainer">
      <div id="sortHeader">
        <div id="sortControlsList">
          {
            sortingOptions.map(option => (
              <Button variant="outlined" disableRipple={true} color="secondary" key={"sort-button-"+option.title}>
                {option.title}
                <div className="sortArrows">
                  <ArrowDropUpIcon
                    className={`upArrow ${orderBy === option.orderBy && orderDirection === "asc" ? "selected" : "unselected"}`}
                    onClick={() => { setOrderBy(option.orderBy); setOrderDataType(option.orderDataType); setOrderDirection("asc") }}
                  />
                  <ArrowDropDownIcon
                    className={`downArrow ${orderBy === option.orderBy && orderDirection === "desc" ? "selected" : "unselected"}`}
                    onClick={() => { setOrderBy(option.orderBy); setOrderDataType(option.orderDataType); setOrderDirection("desc") }}
                  />
                </div>
              </Button>
            ))
          }
        </div>
        <div id="sortClearContainer">
          <Button
            variant="outlined"
            disableRipple={true}
            color="secondary"
            onClick={() => { dispatch(clearAll()); setOrderBy("createdAt"); setOrderDataType("date"); setOrderDirection("asc") }}
          >
            CLEAR ALL
          </Button>
        </div>
      </div>
      <div id="forexRateCalculatorList">
        {
          sortedPairs.map((currencyPair, index) =>
            <ForexRateCalculator
              from={currencyPair.from}
              to={currencyPair.to}
              backgroundColor={currencyPair.backgroundColor}
              id={currencyPair.id}
              updatedAt={currencyPair.updatedAt}
              key={currencyPair.id}
            />
          )
        }
      </div>
      {
        !isAddCurrencyPairsModalOpen &&
        <div id="fabContainer">
          <Fab color="turquoise" size="medium" aria-label="add" onClick={() => dispatch(openModal())}>
            <AddIcon />
          </Fab>
        </div>
      }
    </div>
  );
}